/* App.module.css */

/* Global styles */
.container {
  padding: 24px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.largeTitle {
  margin-bottom: 16px;
}

.cardContainer {
  display: flex;
  gap: 20px;
  margin-top: 24px;
}

.card {
  flex: 1;
  padding: 16px;
}

.cardContent {
  padding: 16px;
}

.title1 {
  font-size: 1.25rem; /* Adjust based on your preferred size */
}

.button {
  white-space: nowrap;
  max-width: 200px;
}

.buttonContainer {
  display: flex;
  gap: 8px;
}

.image {
  width: 140px;
  height: 140px;
  margin-bottom: 12px;
}

.accordionHeader {
  font-size: 1.5rem;
  font-weight: 600;
}

.accordionPanel {
  font-size: 1rem;
}
