/* FileUploadPage.module.css */

.container {
  padding: 16px;
  max-width: 800px;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.title {
  margin-bottom: 4px;
}

.subtitle {
  color: #605e5c;
  margin-bottom: 16px;
  margin-top: 0;
}

.uploadCard {
  border: 2px dashed #d1d1d1;
  border-radius: 4px;
  padding: 32px 16px;
  text-align: center;
  background: #fafafa;
  margin-bottom: 16px;
}

.uploadCard.dragActive {
  border-color: #742774 !important;
  background: #f5f5f5 !important;
}

.uploadArea {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.uploadIconContainer {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background: #8a8886;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto 12px;
}

.uploadIcon {
  color: white;
  font-size: 20px;
}

.uploadMainText {
  margin: 0 0 4px 0;
}

.uploadSubText {
  color: #605e5c;
  margin-bottom: 12px;
  margin-top: 0;
}

.browseButton {
  cursor: pointer;
}

.accordion {
  margin-bottom: 16px;
}

.requirementText {
  margin: 4px 0;
}

.filesSection {
  margin-top: 16px;
}

.filesHeader {
  display: flex;
  justify-content: space-between;
  padding: 8px 12px;
  background: #f3f2f1;
  border: 1px solid #e1dfdd;
  border-bottom: none;
}

.filesList {
  border: 1px solid #e1dfdd;
}

.fileItem {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px;
  background: white;
}

.fileItem:not(:last-child) {
  border-bottom: 1px solid #e1dfdd;
}

.fileInfo {
  display: flex;
  align-items: center;
  gap: 8px;
  flex: 1;
  min-width: 0;
}

.fileDetails {
  min-width: 0;
  flex: 1;
}

.fileName {
  margin: 0 0 2px 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.fileStatus {
  margin: 0;
}

.fileStatus.uploaded {
  color: #107c10;
}

.fileStatus.failed {
  color: #d13438;
}

.fileStatus.loading {
  color: #605e5c;
}